@font-face {
  font-family: 'Akira Expanded';
  src: url('../fonts/Akira\ Expanded\ Demo.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

html {
  font-size: 62.5%;
}
body {
  font-size: 1.6rem;
}
li {
  list-style: none;
}

button {
  background: none;
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  cursor: pointer;
  padding: 0;
}

button:focus {
  outline: none;
}

a, button.nav__link {
  text-decoration: none;
  color: rgb(204, 204, 204);
}

a:hover, button.nav__link:hover {
  color: #e96f7c;
}

/* Modified .nav class */
.nav {
  position: fixed; /* Added to enable z-index */
  top: 0; /* Ensure it stays at the top */
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 7.5%;
  margin-right: 7.5%;
  height: 8vh;
  z-index: 4; /* Higher than grain-overlay (z-index:2) */
  font-family: 'Akira Expanded', sans-serif;
  width: 85%; /* To complement left and right margins */
}

/* Ensure the navigation menu is above the grain overlay in mobile view */
.nav__menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10rem;
}

.nav__toggler {
  position: absolute; /* Asegura que esté en la parte superior derecha */
  top: 2rem;
  right: 2rem;
  z-index: 10; /* Asegúrate de que esté por encima de otros elementos */
  display: block;
  cursor: pointer;
}


.nav__toggler div {
  width: 2.5rem;
  height: 0.2rem;
  margin: 0.4rem;
  margin-right: 3rem;
  background: #fff;
  transition: 0.3s ease-in;
}



















.home__icon {
  font-size: 3rem;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.home-icon {
  color: #fff; /* Color para la estrella */
  transition: transform 0.5s ease, opacity 0.3s ease, box-shadow 0.5s ease;
}

/* Añade la clase 'shrink' para reducir y desaparecer el ícono */
.home-icon.shrink {
  transform: scale(0); /* Reduce el tamaño a 0 */
  opacity: 0; /* Desaparece */
}

/* Cuando crece de nuevo al tamaño original */
.home__icon.grow {
  animation: grow 0.3s forwards ease-out;
}

@keyframes grow {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

/* Efecto de sombra después de agrandarse */
.home-icon.shadow {
  text-shadow: 0 0 10px rgba(255, 255, 255, 0.8); /* Añade una sombra a la estrella */
  transform: scale(1); /* Asegúrate de que vuelva al tamaño original */
}






















@media screen and (max-width: 768px) {
  .nav__toggler {
    display: block;
    cursor: pointer;
  }

  .nav__menu {
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    padding: 0%;
    background: url('../imgs/grain-dark.gif') repeat center center;
    flex-direction: column;
    transform: translateX(100%);
    transition: 0.5s ease-in;
    margin: 0px;
    justify-content: center;
    gap: 20rem;
    overflow: hidden;
    z-index: 5; /* Increased to be above grain-overlay (z-index:2) */
  }

  /* Remove or adjust z-index of child elements if necessary */
  /* Removed .nav__menu > * { z-index: 1; } */

  .nav__active {
    transform: translateX(0%);
  }

  .toggle .line1 {
    transform: rotate(-45deg) translate(-4px, 5px);
  }
  .toggle .line2 {
    opacity: 0;
  }
  .toggle .line3 {
    transform: rotate(45deg) translate(-4px, -5px);
  }
}
